:root {
  --bg-color: #eee;
  --term-bg-color: #272822;
  --term-fg-color: #fff;
  --base-unit: 1vw;
  --corner-radius: calc(0.6 * var(--base-unit));
  --half-corner-radius: calc(var(--corner-radius) / 2);
  --main-font-size: calc(1.2 * var(--base-unit));
  --main-font: "Open Sans", sans-serif;
  --shadow-logo-size: calc(0.3 * var(--base-unit));
  --logo-height: 12vh;
  --term-height: 60vh;
  --term-font-size: 12pt;
  --term-font: "Monaco", "Menlo", "Ubuntu Mono", "Consolas", "source-code-pro",
    monospace;
  --term-script-color: var(--python-grey);
  --main-container-width: 90%;
  --main-container-height: 75vh;
  --left-container-width: 50%;
  --right-container-width: calc(100% - var(--left-container-width) - 2%);
  --shadow-color: rgba(0, 0, 0, 0.5);
  --link-color: #888;
  --subtitle-color: var(--link-color);
  --python-yellow: #ffc938;
  --python-blue: #366f9e;
  --python-grey: #aaa;
  --notification-font-size: 1.1rem;
}

[data-theme="light"] {
  --term-bg-color: #fff;
  --term-fg-color: #272822;
}

.root {
  font-family: var(--main-font);
  font-size: var(--main-font-size);

  background-color: var(--bg-color);
  font-family: var(--main-font);
  font-size: var(--main-font-size);
  height: 100%;
  min-height: 100vh;
  overflow: auto;
}

.header {
  font-size: 0;

  &[data-compact="true"] {
    display: block;
    position: absolute;
    right: 0;
    bottom: 0;
    overflow: hidden;
    transform: rotate(-90deg) translateX(80%) translateY(200%);

    .title {
      font-size: var(--main-font-size);
    }

    .subtitle {
      display: none;
    }

    .basthon-logo {
      filter: drop-shadow(0.1vw 0.1vw 0.1vw rgba(0, 0, 0, 0.5));
      vertical-align: sub;
      width: calc(var(--base-unit) * 7);
      height: unset;
    }
  }
}

.title {
  text-align: center;
}

.subtitle {
  text-align: center;
  font-size: var(--main-font-size);
  margin-bottom: calc(0.5 * var(--base-unit));
  color: var(--subtitle-color);
}

textarea {
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box; /* Firefox, other Gecko */
  box-sizing: border-box; /* Opera/IE 8+ */
}

textarea:focus {
  outline: none !important;
}

.basthon-logo {
  height: var(--logo-height);
  filter: drop-shadow(
    var(--shadow-logo-size) var(--shadow-logo-size) var(--shadow-logo-size)
      var(--shadow-color)
  );
  margin: 0px;
}

.container {
  width: var(--main-container-width);
  height: var(--main-container-height);
  position: relative;
  margin: auto;

  &[data-compact-header] {
    margin-top: 2vw;
  }
}

.left-panel {
  width: var(--left-container-width);
  margin: auto;
  float: left;

  &[data-view-mode="reversed"] {
    float: right;
  }

  &[data-view-mode="script-only"] {
    width: 100%;
  }

  &[data-view-mode="terminal-only"] {
    display: none;
  }
}

.right-panel {
  width: var(--right-container-width);
  position: relative;
  margin: auto;
  float: right;
  font-size: 0;

  &[data-view-mode="reversed"] {
    float: left;
  }

  &[data-view-mode="terminal-only"] {
    width: 100%;
  }

  &[data-view-mode="script-only"] {
    display: none;
  }
}

%darklighted {
  background-color: var(--term-bg-color);
  --background: var(--term-bg-color);
  color: var(--term-fg-color);
  --color: var(--term-fg-color);
}

.editor {
  @extend %darklighted;

  float: none;
  width: 100%;
  height: var(--term-height);
  border-radius: var(--corner-radius);
  margin: 0px;
  padding-top: 0px;
}

.terminal {
  @extend %darklighted;

  position: relative;
  width: 100%;
  height: var(--term-height);
  float: none;
  resize: none;
  font-family: var(--term-font);
  font-size: var(--term-font-size);
  margin: 0px;
  border-radius: var(--corner-radius);
  border-width: 0px;

  &:not([data-right-panel="terminal"]) {
    display: none;
  }
}

.graphics {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: var(--term-height);
  float: none;
  flex: initial;
  background-color: var(--bg-color);
  resize: none;
  box-sizing: border-box;
  margin-bottom: 0px;
  padding: 0px;
  border-style: dotted;
  border-color: var(--term-bg-color);
  border-radius: var(--corner-radius);
  border-width: calc(0.25 * var(--base-unit));
  font-size: var(--term-font-size);

  &:not([data-right-panel="graphics"]) {
    display: none;
  }
}

.graphics > :is(img, svg) {
  display: block;
  width: 100%;
  height: 100%;
}

.graphics > :is(img) {
  object-fit: contain;
}

.graphics > .animation > .anim-controls > .anim-buttons > button {
  padding: revert;
  width: unset;
}

.buttons-container {
  display: flex;
  float: left;
  padding: var(--base-unit) 0px 0px 0px;
}

@keyframes btn-eye-catcher-animation {
  50% {
    box-shadow:
      0px 0px calc(0.5 * var(--base-unit)) calc(1 * var(--base-unit)) #ffc938,
      0 var(--corner-radius) #999;
  }
}

.btn-eye-catcher {
  animation: btn-eye-catcher-animation 0.5s ease infinite;
}

.btn {
  background-color: var(--python-blue);
  border: none;
  border-radius: var(--corner-radius);
  color: white;
  width: calc(5.5 * var(--base-unit));
  height: calc(3.8 * var(--base-unit));
  text-align: center;
  text-decoration: none;
  display: block;
  font-size: calc(1.5 * var(--base-unit));
  box-shadow: 0 var(--corner-radius) #999;
  margin-right: calc(1 * var(--base-unit));

  &[data-titled="true"] {
    width: unset;
    font-size: var(--main-font-size);
    padding: 0 calc(1.8 * var(--base-unit));

    & > svg {
      font-size: calc(1.5 * var(--base-unit));
      padding-right: calc(0.7 * var(--base-unit));
    }
  }

  &:hover {
    background-color: var(--python-yellow);
    cursor: pointer;
  }

  &:enabled:active {
    box-shadow: 0 calc(0.2 * var(--base-unit)) #666;
    transform: translateY(calc(0.3 * var(--base-unit)));
  }
}

.btn-logo {
  /*display: block*/
}

a {
  color: var(--link-color);
  text-decoration: underline;

  &:hover {
    text-decoration: none;
  }
}

.footer {
  position: absolute;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

.help {
  font-size: calc(3 * var(--base-unit));

  > svg {
    display: block;
    padding: 1vh;
  }
}

:global {
  .basthon-loader-root.basthon-loader-foot {
    border-radius: var(--corner-radius) var(--corner-radius) 0px 0px;
    font-family: var(--main-font);
    font-size: var(--main-font-size);
  }

  .basthon-dynamic-iframe {
    flex: 1 0 auto;
  }

  .matplotlib-toolbar-button {
    display: inline-block;
    font-style: normal;
    font-variant: normal;
    line-height: 1;
    text-rendering: auto;
    font-weight: 900;
  }

  .darklighted {
    background-color: var(--term-bg-color);
    --background: var(--term-bg-color);
    color: var(--term-fg-color);
    --color: var(--term-fg-color);
  }

  // increase specificity to bypass .terminal { height: auto; }
  div > div > .terminal {
    height: var(--term-height);
  }

  .terminal {
    --size: 1.333;
  }

  /* clickable script content */
  .terminal-output > :not(.raw) div.exec-script-header:hover {
    background-color: var(--term-script-color);
    border-radius: var(--half-corner-radius);
    cursor: pointer;
  }

  /* to force x-scroll */
  .terminal-output div[style*="100%;"] {
    overflow: unset;
  }

  .notie-container {
    font-size: var(--notification-font-size);
  }

  .notie-background-success {
    background-color: var(--python-yellow);
  }

  .notie-background-warning {
    background-color: var(--python-blue);
  }

  .notie-background-error {
    background-color: var(--python-blue);
  }

  .notie-background-info {
    background-color: var(--python-grey);
  }
}
